"use client";

import { useState, useEffect } from "react";
import {
  Box,
  Grid,
  Button,
  TextField,
  MenuItem,
  IconButton,
  Select,
  FormControl,
  InputLabel,
  Typography,
  OutlinedInput,
  Autocomplete,
  Stack,
  useMediaQuery,
  useTheme,
} from "@mui/material";

import { SwapHoriz, Search } from "@mui/icons-material";

// Example countriesAirports array
const countriesAirports = [
  { city: "Dubai", airport: "Dubai International Airport", code: "DXB" },
  { city: "Abu Dhabi", airport: "Abu Dhabi Intl Airport", code: "AUH" },
  { city: "Karachi", airport: "Jinnah International Airport", code: "KHI" },
  { city: "Lahore", airport: "Allama Iqbal Airport", code: "LHE" },
  { city: "Delhi", airport: "Indira Gandhi International Airport", code: "DEL" },
  { city: "Mumbai", airport: "Chhatrapati Shivaji Maharaj Airport", code: "BOM" },
  { city: "Riyadh", airport: "King Khalid Intl Airport", code: "RUH" },
  { city: "Jeddah", airport: "King Abdulaziz Intl Airport", code: "JED" },
  { city: "Berlin", airport: "Berlin Brandenburg Airport", code: "BER" },
  { city: "Frankfurt", airport: "Frankfurt Airport", code: "FRA" },
];

export default function FlightSearchForm() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  
  // ---------------- STATES ----------------
  const [flyingFrom, setFlyingFrom] = useState("");
  const [destinationTo, setDestinationTo] = useState("");
  const [selectedTripType, setSelectedTripType] = useState("one-way");
  const [selectedCabinClass, setSelectedCabinClass] = useState("economy");
  const [travellers, setTravellers] = useState({
    adults: 1,
    children: 0,
    infants: 0,
  });
  const [departDate, setDepartDate] = useState(null);
  const [returnDate, setReturnDate] = useState(null);

  // Initial dates
  useEffect(() => {
    const today = new Date();
    const plus7 = new Date();
    plus7.setDate(today.getDate() + 7);
    setDepartDate(today);
    setReturnDate(plus7);
  }, []);

  const tripTypeOptions = [
    { value: "one-way", label: "One Way" },
    { value: "return", label: "Return" },
  ];

  const cabinClassOptions = [
    { value: "economy", label: "Economy" },
    { value: "premium-economy", label: "Premium Economy" },
    { value: "business", label: "Business" },
    { value: "first-class", label: "First Class" },
  ];

  // Format date for input
  const formatDate = (date) => {
    if (!date) return "";
    const d = new Date(date);
    const year = d.getFullYear();
    const month = String(d.getMonth() + 1).padStart(2, '0');
    const day = String(d.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  };

  // ----------------- JSX -----------------
  return (
    <Box sx={{ 
      p: isMobile ? 1 : 2, 
      maxWidth: 900, 
      mx: "auto" 
    }}>
      {/* Trip type + cabin - Mobile centered */}
      <Grid container spacing={isMobile ? 1 : 2} mb={2}
        sx={{
          justifyContent: isMobile ? "center" : "flex-start",
          flexDirection: isMobile ? "column" : "row",
          alignItems: isMobile ? "center" : "flex-start"
        }}
      >
        <Grid item xs={6} sm={3} sx={{ 
          width: isMobile ? "100%" : "auto",
          display: "flex",
          justifyContent: isMobile ? "center" : "flex-start"
        }}>
          <FormControl fullWidth size="small" sx={{ 
            px: isMobile ? 0 : 0.5,
            maxWidth: isMobile ? "300px" : "100%"
          }}>
            <Select
              value={selectedTripType}
              onChange={(e) => setSelectedTripType(e.target.value)}
              sx={{
                fontSize: isMobile ? "12px" : "10px",
                fontWeight: "800",
                color: "#000000",
                borderColor: "#c0c0c0",
                "&:hover": { 
                  backgroundColor: "#f0f7ff", 
                  borderColor: "#1976d2",
                  "& .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#1976d2"
                  }
                },
                height: isMobile ? 36 : 24,
              }}
            >
              {tripTypeOptions.map((option) => (
                <MenuItem
                  key={option.value}
                  value={option.value}
                  sx={{ fontSize: isMobile ? "12px" : "10px" }}
                >
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>

        <Grid item xs={6} sm={3} sx={{ 
          width: isMobile ? "100%" : "auto",
          display: "flex",
          justifyContent: isMobile ? "center" : "flex-start"
        }}>
          <FormControl fullWidth size="small" sx={{ 
            maxWidth: isMobile ? "300px" : "100%"
          }}>
            <Select
              value={selectedCabinClass}
              onChange={(e) => setSelectedCabinClass(e.target.value)}
              sx={{
                fontSize: isMobile ? "12px" : "10px",
                fontWeight: "800",
                color: "#000000",
                borderColor: "#c0c0c0",
                "&:hover": { 
                  backgroundColor: "#f0f7ff", 
                  borderColor: "#1976d2",
                  "& .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#1976d2"
                  }
                },
                height: isMobile ? 36 : 24,
              }}
            >
              {cabinClassOptions.map((option) => (
                <MenuItem
                  key={option.value}
                  value={option.value}
                  sx={{ fontSize: isMobile ? "12px" : "10px" }}
                >
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
      </Grid>

      {/* Main Grid */}
      {isMobile ? (
        // MOBILE LAYOUT - STACKED COLUMNS
        <Stack spacing={2}>
          {/* Flying From and Destination To */}
          <Stack spacing={1.5}>
            <Autocomplete
              options={countriesAirports}
              getOptionLabel={(option) => `${option.city} (${option.code})`}
              value={countriesAirports.find((a) => a.code === flyingFrom) || null}
              onChange={(event, newValue) => setFlyingFrom(newValue ? newValue.code : "")}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Flying From"
                  size="small"
                  sx={{
                    "& .MuiOutlinedInput-root": {
                      height: 44,
                      fontSize: "14px",
                      "& fieldset": {
                        borderColor: "#c0c0c0",
                      },
                      "&:hover fieldset": {
                        borderColor: "#1976d2",
                      },
                      "&:hover": { 
                        backgroundColor: "#f0f7ff",
                      },
                    },
                    "& .MuiInputLabel-root": {
                      fontSize: "14px",
                    },
                  }}
                />
              )}
            />

            <Box sx={{ display: "flex", justifyContent: "center" }}>
              <IconButton
                onClick={() => {
                  const temp = flyingFrom;
                  setFlyingFrom(destinationTo);
                  setDestinationTo(temp);
                }}
                size="small"
                sx={{
                  bgcolor: "#3366ff",
                  color: "white",
                  height: 36,
                  width: 36,
                  zIndex: 10,
                  "&:hover": { bgcolor: "#00BFFF" },
                  transform: "rotate(90deg)",
                }}
              >
                <SwapHoriz sx={{ fontSize: 20 }} />
              </IconButton>
            </Box>

            <Autocomplete
              options={countriesAirports}
              getOptionLabel={(option) => `${option.city} (${option.code})`}
              value={countriesAirports.find((a) => a.code === destinationTo) || null}
              onChange={(event, newValue) => setDestinationTo(newValue ? newValue.code : "")}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Destination To"
                  size="small"
                  sx={{
                    "& .MuiOutlinedInput-root": {
                      height: 44,
                      fontSize: "14px",
                      "& fieldset": {
                        borderColor: "#c0c0c0",
                      },
                      "&:hover fieldset": {
                        borderColor: "#1976d2",
                      },
                      "&:hover": { 
                        backgroundColor: "#f0f7ff",
                      },
                    },
                    "& .MuiInputLabel-root": {
                      fontSize: "14px",
                    },
                  }}
                />
              )}
            />
          </Stack>

          {/* Dates - Mobile Stacked */}
          <Stack spacing={1.5}>
            <TextField
              type="date"
              label="Depart Date"
              value={formatDate(departDate)}
              onChange={(e) => setDepartDate(new Date(e.target.value))}
              size="small"
              fullWidth
              InputLabelProps={{ shrink: true }}
              sx={{
                "& .MuiOutlinedInput-root": {
                  height: 44,
                  fontSize: "14px",
                  "& fieldset": {
                    borderColor: "#c0c0c0",
                  },
                  "&:hover fieldset": {
                    borderColor: "#1976d2",
                  },
                  "&:hover": {
                    backgroundColor: "#f0f7ff",
                  },
                },
              }}
            />
            
            {selectedTripType === "return" && (
              <TextField
                type="date"
                label="Return Date"
                value={formatDate(returnDate)}
                onChange={(e) => setReturnDate(new Date(e.target.value))}
                size="small"
                fullWidth
                InputLabelProps={{ shrink: true }}
                sx={{
                  "& .MuiOutlinedInput-root": {
                    height: 44,
                    fontSize: "14px",
                    "& fieldset": {
                      borderColor: "#c0c0c0",
                    },
                    "&:hover fieldset": {
                      borderColor: "#1976d2",
                    },
                    "&:hover": {
                      backgroundColor: "#f0f7ff",
                    },
                  },
                }}
              />
            )}
          </Stack>

          {/* Travellers and Search - Mobile */}
          <Stack spacing={1.5}>
            <TravellersDropdown
              travellers={travellers}
              onTravellersChange={setTravellers}
              sx={{
                "& .MuiButton-root": {
                  height: 44,
                  fontSize: "14px",
                  borderColor: "#c0c0c0",
                  "&:hover": { 
                    borderColor: "#1976d2", 
                    backgroundColor: "#f0f7ff" 
                  },
                },
              }}
            />

            <Button
              variant="contained"
              color="primary"
              startIcon={<Search sx={{ fontSize: 20 }} />}
              fullWidth
              sx={{
                height: 44,
                fontSize: "16px",
                fontWeight: 600,
                textTransform: "none",
                "&:hover": { 
                  backgroundColor: "#1565c0",
                },
              }}
            >
              Search Flights
            </Button>
          </Stack>
        </Stack>
      ) : (
        // DESKTOP LAYOUT - ORIGINAL
        <Grid container spacing={1} alignItems="center" pl={0.4}>
          {/* Flying From and Destination To with Swap */}
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              position: "relative",
              gap: 1
            }}
          >
            {/* Flying From */}
            <Autocomplete
              options={countriesAirports}
              getOptionLabel={(option) => `${option.city} (${option.code})`}
              value={countriesAirports.find((a) => a.code === flyingFrom) || null}
              onChange={(event, newValue) => setFlyingFrom(newValue ? newValue.code : "")}
              sx={{
                width: 186,
                "& .MuiAutocomplete-option": {
                  fontSize: "8px",
                },
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Flying From"
                  size="small"
                  sx={{
                    "& .MuiOutlinedInput-root": {
                      height: 40,
                      fontSize: "12px",
                      "& fieldset": {
                        borderColor: "#c0c0c0",
                      },
                      "&:hover fieldset": {
                        borderColor: "#1976d2",
                      },
                      "&:hover": { 
                        backgroundColor: "#f0f7ff",
                      },
                    },
                    "& .MuiInputLabel-root": {
                      fontSize: "12px",
                      top: "2px",
                      left:"4px"
                    },
                  }}
                />
              )}
            />

            {/* Swap Button */}
            <IconButton
              onClick={() => {
                const temp = flyingFrom;
                setFlyingFrom(destinationTo);
                setDestinationTo(temp);
              }}
              size="small"
              sx={{
                position: "absolute",
                left: "50%",
                top: "5px",
                transform: "translateX(-50%)",
                zIndex: 10,
                bgcolor: "#3366ff",
                color: "white",
                height: 30,
                width: 30,
                "&:hover": { bgcolor: "#00BFFF" },
              }}
            >
              <SwapHoriz sx={{ fontSize: 20 }} />
            </IconButton>

            {/* Destination To */}
            <Autocomplete
              options={countriesAirports}
              getOptionLabel={(option) => `${option.city} (${option.code})`}
              value={countriesAirports.find((a) => a.code === destinationTo) || null}
              onChange={(event, newValue) => setDestinationTo(newValue ? newValue.code : "")}
              sx={{
                width: 186,
                "& .MuiAutocomplete-option": {
                  fontSize: "12px",
                },
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Destination To"
                  size="small"
                  sx={{
                    "& .MuiOutlinedInput-root": {
                      height: 40,
                      fontSize: "12px",
                      "& fieldset": {
                        borderColor: "#c0c0c0",
                      },
                      "&:hover fieldset": {
                        borderColor: "#1976d2",
                      },
                      "&:hover": { 
                        backgroundColor: "#f0f7ff",
                      },
                    },
                    "& .MuiInputLabel-root": {
                      fontSize: "12px",
                      top: "2px",
                      left:"4px"
                    },
                  }}
                />
              )}
            />
          </Box>

          {/* Dates */}
          <Box sx={{ display: "flex", gap: 1, width: 230 }}>
            {/* Depart Date */}
            <FormControl fullWidth variant="outlined" size="small">
              <InputLabel shrink sx={{ fontSize: "12px", top: "-3px" }}>
                Depart Date
              </InputLabel>
              <OutlinedInput
                type="date"
                value={formatDate(departDate)}
                onChange={(e) => setDepartDate(new Date(e.target.value))}
                inputProps={{
                  style: {
                    height: 40,
                    padding: "0 8px",
                    fontSize: "12px",
                  },
                }}
                sx={{
                  height: 40,
                  "& .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#c0c0c0",
                  },
                  "&:hover .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#1976d2",
                  },
                  "&:hover": {
                    backgroundColor: "#f0f7ff",
                  },
                }}
                label="Depart Date"
              />
            </FormControl>

            {/* Return Date (only on Return trip) */}
            {selectedTripType === "return" && (
              <FormControl fullWidth variant="outlined" size="small">
                <InputLabel shrink sx={{ fontSize: "12px", top: "-3px" }}>
                  Return Date
                </InputLabel>
                <OutlinedInput
                  type="date"
                  value={formatDate(returnDate)}
                  onChange={(e) => setReturnDate(new Date(e.target.value))}
                  inputProps={{
                    style: {
                      height: 40,
                      padding: "0 8px",
                      fontSize: "12px",
                    },
                  }}
                  sx={{
                    height: 40,
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#c0c0c0",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#1976d2",
                    },
                    "&:hover": {
                      backgroundColor: "#f0f7ff",
                    },
                  }}
                  label="Return Date"
                />
              </FormControl>
            )}
          </Box>

          {/* Travellers */}
          <Grid item xs={12} sm={2}>
            <TravellersDropdown
              travellers={travellers}
              onTravellersChange={setTravellers}
              size="small"
              sx={{
                height: 40,
                "& .MuiButton-root": {
                  height: 40,
                  fontSize: "12px",
                  borderColor: "#c0c0c0",
                  "&:hover": { 
                    borderColor: "#1976d2", 
                    backgroundColor: "#f0f7ff" 
                  },
                },
              }}
            />
          </Grid>

          {/* Search */}
          <Grid item xs={12} sm={1}>
            <Button
              variant="contained"
              color="primary"
              startIcon={<Search sx={{ height: 40, pl: "12px", py: "5px" }} />}
              sx={{
                width: "60px",
                alignItems: "center",
                height: 40,
                margin: "auto",
                "&:hover": { 
                  backgroundColor: "#1565c0",
                },
              }}
            ></Button>
          </Grid>
        </Grid>
      )}
    </Box>
  );
}